<template>
  <div id="EditPlantPage" class="pa-0">
    <v-card v-if="plant">
      <v-card-title class="title white--text pa-0" :class="plant.vue_color || 'secondary'">
        <v-avatar tile color="white" size="55" class="ml-0 mr-2">
          <v-icon medium :color="plant.vue_color" class="mx-2">edit</v-icon>
        </v-avatar>
        <span class="mr-3">{{ plant.gr }}</span>
      </v-card-title>
      <v-divider></v-divider>
      <v-card-text>
        Σελίδα επεξεργασίας του κηπευτικού <b><em>{{ plant.latin }}</em></b>.
      </v-card-text>
      <v-card-actions>
        <v-btn flat fab color="green darken-1" :loading="saving">
          <v-icon medium>save</v-icon>
        </v-btn>
        <v-spacer></v-spacer>
        <v-btn flat fab color="red darken-1" @click="removePlant()" :loading="deleting">
          <v-icon medium>delete_forever</v-icon>
        </v-btn>
      </v-card-actions>
    </v-card>
    <v-card v-else class="my-5">
      <v-card-title class="title white--text pa-0 secondary">
        <v-avatar tile color="white" size="55" class="mr-3">
          <v-icon medium>error</v-icon>
        </v-avatar>
        <span>Σφάλμα αναζήτησης</span>
      </v-card-title>
      <v-divider></v-divider>
      <v-card-text class="pa-0">
        <v-alert type="error" class="ma-0 pa-4 subheading" :value="true">
          Το κηπευτικό <b>{{ $route.params.gr }}</b> δεν βρέθηκε. Διορθώστε τη διεύθυνση <b>URL</b> ή μεταβείτε στη σελίδα <router-link class="white--text" to="/diagnosis">Διάγνωσης</router-link> για πλήρη κατάλογο των κηπευτικών του iGarden.
        </v-alert>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>
import API from '@/lib/API'
import swal from 'sweetalert2'
export default {
  name: 'EditPlantPage',
  props: ['chosenPlant', 'retrievedPlants'],
  data () {
    return {
      plant: null,
      plants: this.retrievedPlants,
      error: false,
      deleting: false,
      saving: false
    }
  },
  created () {
    this.plant = this.plants.filter(plant => plant.gr === this.chosenPlant)[0]
  },
  mounted () {
    if (!this.plant) {
      this.error = true
    }
  },
  methods: {
    removePlant () {
      swal({
        type: 'warning',
        title: 'Προσοχή!',
        html: `Πρόκειται να διαγράψετε το φυτό <b>${this.plant.gr}</b> οριστικά!\nΘέλετε σίγουρα να προχωρήσετε;`,
        showCancelButton: true,
        confirmButtonText: 'Διαγραφή',
        confirmButtonColor: 'red',
        cancelButtonText: 'Ακύρωση',
        reverseButtons: true
      }).then(willDelete => {
        this.deleting = true
        if (willDelete.value) {
          API.deletePlant(this.plant._id.$oid).then(deletedPlant => {
            this.deleting = false
            swal({
              type: 'success',
              title: 'Επιτυχής διαγραφή!',
              html: `Το φυτό <b>${deletedPlant.gr}</b> αφαιρέθηκε επιτυχώς από το αρχείο! Πατώντας <b>ΟΚ</b> θα μεταφερθείτε αυτόματα στη σελίδα διάγνωσης.`
            }).then(ok => this.$router.replace('/diagnosis'))
          })
        } else { this.deleting = false }
      })
    },
    savePlant () {}
  }
}
</script>

<style lang="css">
.whitePanel{
  background-color: rgba(255,255,255,0.55);
}
</style>



