<template>
  <v-container fluid grid-list-md class="pa-0">
    <v-layout row wrap>
      <v-flex v-for="(plant, i) in plants" :key="i" sm12 md6 lg4>
        <v-card>
          <progressive-img
            :src="plant.eng ? `/static/plants/${plant.eng}.jpg` : '/static/unknownPlant.jpg'"
            class="mr-0"
            placeholder="/static/placeholder.png"
            :blur="3"
            :aspect-ratio="0.8">
          </progressive-img>
          <v-card-text class="pa-0">
            <v-flex class="pa-0">
              <v-list class="pa-0" two-line>
                <v-list-tile :href="wikipedia(plant.gr)" :class="plant.vue_color || 'secondary'" class="white--text" target="_blank">
                  <v-list-tile-avatar>
                    <v-icon color="white" medium>info</v-icon>
                  </v-list-tile-avatar>
                  <v-list-tile-content>
                    <v-list-tile-title>{{ plant.gr }}</v-list-tile-title>
                    <v-list-tile-sub-title class="white--text">Κοινή Ονομασία</v-list-tile-sub-title>
                  </v-list-tile-content>
                  <v-list-tile-action><v-icon color="white" medium>link</v-icon></v-list-tile-action>
                </v-list-tile>
                <PlantTile :plant="plant" avatar="school" sub="Επιστημονική Ονομασία">
                  <span slot="title"><em>{{ plant.latin }}</em></span>
                </PlantTile>
                <v-divider :class="plant.vue_color + ' lighten-4'" inset></v-divider>
                <PlantTile :route="`/edit/${plant.gr}`" :plant="plant" avatar="edit" :action="true" sub="Επεξεργασία κηπευτικού">
                  <span slot="title">Επεξεργασία</span>
                </PlantTile>
                <v-divider :class="plant.vue_color + ' lighten-4'" inset></v-divider>
                <PlantTile :route="`/diagnosis/${plant.gr}`" :plant="plant" avatar="library_books" :action="true" sub="Διαγνωστικός οδηγός"
                  :hasNotDiseases="plant.diseases.length == 0">
                  <span slot="title">Διάγνωση</span>
                </PlantTile>
                <v-divider :class="plant.vue_color + ' lighten-4'" inset></v-divider>
                <ListGroup icon="local_hospital" :color="plant.vue_color" title="Ασθένειες"
                  :sub="plant.diseases.length > 0 ? `${plant.diseases.length} στο αρχείο` : 'Καμία'">
                  <template slot="items">
                    <CatalogDiseaseList :plant="plant"></CatalogDiseaseList>
                  </template>
                </ListGroup>
                <!-- <v-tooltip bottom>
                  <v-list-tile
                    slot="activator"
                    @click="showDiseaseList(plant)"
                    :disabled="plant.diseases.length == 0">
                    <v-list-tile-avatar>
                      <v-icon :color="plant.vue_color" medium>local_hospital</v-icon>
                    </v-list-tile-avatar>
                    <v-list-tile-content>
                      <v-list-tile-title>Ασθένειες</v-list-tile-title>
                      <v-list-tile-sub-title>
                        {{ plant.diseases.length > 0 ? `${plant.diseases.length} στο αρχείο` : 'Καμία'}}
                      </v-list-tile-sub-title>
                    </v-list-tile-content>
                    <v-list-tile-action>
                      <v-icon medium>{{ plant.diseaseExpand ? 'expand_less' : 'expand_more' }}</v-icon>
                    </v-list-tile-action>
                  </v-list-tile>
                  <span>Πατήστε για εμφάνιση του καταλόγου</span>
                </v-tooltip> -->
              </v-list>
            </v-flex>
          </v-card-text>
          <v-slide-y-transition>
            <CatalogDiseaseList :plant="plant" v-if="plant.diseaseExpand"></CatalogDiseaseList>
          </v-slide-y-transition>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import CatalogDiseaseList from './CatalogDiseaseList'
import PlantTile from '@/components/layouts/PlantTile'
import ListGroup from '@/components/layouts/ListGroup'
import { bus } from '@/lib/bus'
export default {
  name: 'PlantsCatalog',
  props: ['plants'],
  components: {
    CatalogDiseaseList,
    PlantTile,
    ListGroup
  },
  data () {
    return {
    }
  },
  computed: {
    wikipedia: function () {
      return plant => `https://el.wikipedia.org/wiki/${plant}`
    }
  },
  methods: {
    showDiseaseList (plant) {
      plant.diseaseExpand = (plant.diseases.length === 0) ? false : !plant.diseaseExpand
    },
    printArray (array) {
      return array.join(', ').toString()
    },
    selectPlant (plant) {
      bus.$emit('choosed-plant', plant)
      this.$router.push(`/diagnosis/${plant}`)
    }
  }
}
</script>

<style lang="css" scoped>
.ntt{
  text-transform: none;
}
</style>



