<template>
  <v-card id="AddPlant" class="pa-0 ma-0">
    <v-card-text class="pa-0">
      <v-form ref="plantForm" @submit="submit">
        <v-list three-line class="pa-0">
          <v-subheader class="subheading lime accent-4 justify-center">{{ subHeader }}</v-subheader>
          <v-divider class="light-green"></v-divider>
          <v-list-tile class="pt-2">
            <v-list-tile-content>
              <v-text-field
                label="Κοινή ονομασία κηπευτικού"
                hint="Συμπληρώστε το όνομα με ελληνικούς χαρακτήρες."
                v-model="plant.gr"
                :loading="adding"
                prepend-icon="local_florist"
                :rules="required"
                required
                @keyup.enter="add">
              </v-text-field>
            </v-list-tile-content>
          </v-list-tile>
          <v-divider></v-divider>
          <v-list-tile class="pt-2">
            <v-list-tile-content>
              <v-text-field
                label="Επιστημονική ονομασία κηπευτικού"
                hint="Συμπληρώστε το όνομα με λατινικούς χαρακτήρες."
                v-model="plant.latin"
                :loading="adding"
                prepend-icon="school"
                :rules="required"
                required
                @keyup.enter="add">
              </v-text-field>
            </v-list-tile-content>
          </v-list-tile>
        </v-list>
      </v-form>
    </v-card-text>
    <v-card-actions class="justify-center">
      <v-btn :loading="adding" fab @click="add" class="lime accent-4" dark>
        <v-icon large>add</v-icon>
      </v-btn>
      <v-btn fab @click="clear" flat><v-icon medium>refresh</v-icon></v-btn>
    </v-card-actions>
  </v-card>
</template>

<script>
import API from '@/lib/API'
import swal from 'sweetalert2'
export default {
  name: 'AddPlant',
  props: ['retrievedPlants', 'subHeader'],
  data () {
    return {
      plants: this.retrievedPlants,
      plant: {
        diseaseExpand: false,
        diseases: []
      },
      loading: false,
      required: [
        v => !!v || 'Απαιτούμενο πεδίο!'
      ],
      adding: false
    }
  },
  mounted () {
  },
  methods: {
    add () {
      if (this.plant.latin === undefined || this.plant.gr === undefined) {
        return swal({
          type: 'error',
          title: 'Κενό πεδίο!',
          html: 'Δεν επιτρέπεται η χρήση κενών πεδίων. Προσπαθήστε ξανά!'
        })
      }
      let grExists = this.plants.filter(plant => plant.gr === this.plant.gr).length > 0
      let latinExists = this.plants.filter(plant => plant.latin === this.plant.latin).length > 0
      if (grExists || latinExists) {
        let existingTerm = grExists ? this.plant.gr : this.plant.latin
        return swal({
          type: 'error',
          title: 'Σφάλμα προσθήκης',
          html: `Το φυτό "<b>${existingTerm}</b>" υπάρχει ήδη στο αρχείο...`
        })
      } else {
        this.adding = true
        API.addPlant(this.plant).then(plant => {
          this.adding = false
          if (plant) {
            this.plants.push(plant)
            swal({
              type: 'success',
              title: 'Επιτυχής προσθήκη!',
              html: `Το φυτό <b>${plant.gr}</b> προστέθηκε επιτυχώς στο αρχείο! Πατώντας <b>OK</b> θα μεταφερθείτε αυτόματα στη σελίδα επεξεργασίας για το κηπευτικό.`
            }).then(ok => this.$router.push({ path: `/edit/${plant.gr}` }, this.$router.go()))
          } else {
            swal({
              type: 'error',
              title: 'Σφάλμα προσθήκης φυτού!',
              html: `Το φυτό <b>${plant.gr}</b> δεν προστέθηκε στο αρχείο!`
            })
          }
        }).catch(error => console.log(error))
      }
    },
    clear () {
      this.$refs.plantForm.reset()
    },
    submit () {
      alert('submitted!')
    }
  }
}
</script>

<style lang="css">
</style>



