<template>
  <CardLayout icon="edit" title="Επεξεργασία" url="/edit">
    <template slot="content">
      <AdminEdit v-if="admin" v-on:logout="admin = false"></AdminEdit>
      <v-card class="my-4" v-else>
        <v-alert transition="slide-x-transition" icon="vpn_key" class="ma-0" color="lime accent-4" :value="true">
          <span class="subheading grey-text text--darken-2">
            Απαιτούνται δικαιώματα διαχειριστή για επεξεργασία της βάσης γνώσεως.
          </span>
        </v-alert>
        <v-card-text>
          <v-form ref="userForm">
            <v-text-field
              @keyup.enter="connect"
              label="Όνομα Χρήστη"
              typoe="text"
              v-model="username"
              prepend-icon="account_circle"
              hint="Όνομα διαχειριστή με λατινικούς χαρακτήρες."
            ></v-text-field>
            <v-text-field
              @keyup.enter="connect"
              label="Κωδικός"
              type="password"
              v-model="password"
              prepend-icon="security"
              hint="Κωδικός διαχειριστή με αλφαριθμητικούς χαρακτήρες."
            ></v-text-field>
          </v-form>
        </v-card-text>
        <v-card-actions class="justify-center">
          <v-btn @click="connect" dark fab color="lime accent-4">
            <v-icon large>person</v-icon>
          </v-btn>
        </v-card-actions>
      </v-card>
    </template>
  </CardLayout>
</template>

<script>
import AdminEdit from '@/components/editActions/AdminEdit'
import CardLayout from '@/components/layouts/CardLayout'
export default {
  name: 'Edit',
  components: {
    AdminEdit,
    CardLayout
  },
  data () {
    return {
      icon: 'edit',
      admin: false,
      username: '',
      password: '',
      adminAlert: true
    }
  },
  mounted () {
    this.admin = (sessionStorage.getItem('user') !== null)
  },
  methods: {
    connect () {
      if (this.$refs.userForm.validate()) {
        if ((this.username === 'admin' || 'αδμιν') && this.password === '309048') {
          var user = {
            username: this.username,
            password: this.password
          }
          sessionStorage.setItem('user', JSON.stringify(user))
          this.admin = true
        } else {
          alert('Λάθος Κωδικός ή Όνομα Χρήστη!')
        }
      }
    }
  }
}
</script>

<style lang="css">
.fwn{
  font-weight: normal;
}
.ntt {
  text-transform: none;
}
.nbr{
  border-radius: 0px;
}
.alert{
  border: none;
}
</style>



